import React, { useEffect, useState } from "react";
import styles from "./Editor.module.css";
import InputControl from "../common/InputControl";
import { X } from "react-feather";
function Editor(props) {
  const sections = props.sections;
  const information = props.information;
  const [activeSectionKey, setActiveSectionKey] = useState(
    Object.keys(sections)[0]
  );
  const [activeInformation, setActiveInformation] = useState(
    information[sections[Object.keys(sections)[0]]]
  );
  const [activeDetailIndex, setActiveDetailIndex] = useState(0);
  const [sectionTitle, setSectionTitle] = useState(
    sections[Object.keys(sections)[0]]
  );
  const [values, setValues] = useState({
    name: activeInformation?.detail?.name || "",
    title: activeInformation?.detail?.title || "",
    email: activeInformation?.detail?.email || "",
    phone: activeInformation?.detail?.phone || "",
    linkedin: activeInformation?.detail?.linkedin || "",
    github: activeInformation?.detail?.github || "",
  });
  const handleSubmission = () => {
    switch (sections[activeSectionKey]) {
      case sections.basicInfo: {
        const tempDetail = {
          name: values.name,
          title: values.title,
          email: values.email,
          phone: values.phone,
          linkedin: values.linkedin,
          github: values.github,
        };
        props.setInformation((prev) => ({
          ...prev,
          [sections.basicInfo]: {
            ...prev[sections.basicInfo],
            detail: tempDetail,
            sectionTitle,
          },
        }));
        break;
      }
      case sections.workExperience: {
        const tempDetail = {
          title: values.title,
          companyName: values.companyName,
          location: values.location,
          startDate: values.startDate,
          endDate: values.endDate,
          points: values.points,
        };
        const tempDetails = [...information[sections.workExperience]?.details];
        tempDetails[activeDetailIndex] = tempDetail;
        props.setInformation((prev) => ({
          ...prev,
          [sections.workExperience]: {
            ...prev[sections.workExperience],
            details: tempDetails,
            sectionTitle,
          },
        }));
        break;
      }
      case sections.education: {
        const tempDetail = {
          title: values.title,
          college: values.college,
          startYear: values.startYear,
          cgpa: values.cgpa,
          endYear: values.endYear,
        };
        const tempDetails = [...information[sections.education]?.details];
        tempDetails[activeDetailIndex] = tempDetail;
        props.setInformation((prev) => ({
          ...prev,
          [sections.education]: {
            ...prev[sections.education],
            details: tempDetails,
            sectionTitle,
          },
        }));
        break;
      }
      case sections.skills: {
        const tempDetail = {
          title: values.title,
          level: values.level,
        };
        const tempDetails = [...information[sections.skills]?.details];
        tempDetails[activeDetailIndex] = tempDetail;
        props.setInformation((prev) => ({
          ...prev,
          [sections.skills]: {
            ...prev[sections.skills],
            details: tempDetails,
            sectionTitle,
          },
        }));
        break;
      }
      case sections.projects: {
        const tempDetail = {
          title: values.title,
          overview: values.overview,
          deploymentLink: values.deploymentLink,
          repoLink: values.repoLink,
          points: values.points,
        };
        const tempDetails = [...information[sections.projects]?.details];
        tempDetails[activeDetailIndex] = tempDetail;
        props.setInformation((prev) => ({
          ...prev,
          [sections.projects]: {
            ...prev[sections.projects],
            details: tempDetails,
            sectionTitle,
          },
        }));
        break;
      }
      case sections.certificates: {
        const tempDetail = {
          title: values.title,
          certificateLink: values.certificateLink,
          issuedBy: values.issuedBy,
          issuedOn: values.issuedOn,
        };
        const tempDetails = [...information[sections.certificates]?.details];
        tempDetails[activeDetailIndex] = tempDetail;
        props.setInformation((prev) => ({
          ...prev,
          [sections.certificates]: {
            ...prev[sections.certificates],
            details: tempDetails,
            sectionTitle,
          },
        }));
        break;
      }
      case sections.other: {
        const tempDetail = {
          other: values.other,
        };
        const tempDetails = [...information[sections.other]?.details];
        tempDetails[activeDetailIndex] = tempDetail;
        props.setInformation((prev) => ({
          ...prev,
          [sections.other]: {
            ...prev[sections.other],
            details: tempDetails,
            sectionTitle,
          },
        }));
        break;
      }
      default:
        break;
    }
  };
  const handlePointUpdate = (value, index) => {
    const tempValues = { ...values };
    if (!Array.isArray(tempValues.points)) tempValues.points = [];
    tempValues.points[index] = value;
    setValues(tempValues);
  };
  const handleAddNew = () => {
    const details = activeInformation?.details;
    if (!details) return;
    const lastDetail = details.slice(-1)[0];
    if (!Object.keys(lastDetail).length) return;
    details?.push({});
    props.setInformation((prev) => ({
      ...prev,
      [sections[activeSectionKey]]: {
        ...information[sections[activeSectionKey]],
        details: details,
      },
    }));
    setActiveDetailIndex(details?.length - 1);
  };
  const handleDeleteDetail = (index) => {
    const details = activeInformation?.details
      ? [...activeInformation?.details]
      : "";
    if (!details) return;
    details.splice(index, 1);
    props.setInformation((prev) => ({
      ...prev,
      [sections[activeSectionKey]]: {
        ...information[sections[activeSectionKey]],
        details: details,
      },
    }));
    setActiveDetailIndex((prev) => (prev === index ? 0 : prev - 1));
  };
  const workExperienceBody = (
    <div className={styles.detail}>
      <div className={styles.row}>
        <InputControl
          label="Role"
          placeholder="Enter Role"
          value={values.title}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, title: e.target.value }));
          }}
        />
        <InputControl
          label="Company Name"
          placeholder="Enter Company Name"
          value={values.companyName}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, companyName: e.target.value }));
          }}
        />
        <InputControl
          label="Location"
          placeholder="Enter Location"
          value={values.location}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, location: e.target.value }));
          }}
        />
      </div>
      <div className={styles.row}>
        <InputControl
          label="Start Date"
          placeholder="Enter Start Date"
          type="date"
          value={values.startDate}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, startDate: e.target.value }));
          }}
        />
        <InputControl
          label="End Date"
          placeholder="Enter End Date"
          value={values.endDate}
          type="date"
          onChange={(e) => {
            setValues((prev) => ({ ...prev, endDate: e.target.value }));
          }}
        />
      </div>
      <div className={styles.column}>
        <label>Enter Work Description</label>
        <InputControl
          placeholder="Line 1"
          value={values.points ? values.points[0] : ""}
          onChange={(e) => {
            handlePointUpdate(e.target.value, 0);
          }}
        />
        <InputControl
          placeholder="Line 2"
          value={values.points ? values.points[1] : ""}
          onChange={(e) => {
            handlePointUpdate(e.target.value, 1);
          }}
        />
        <InputControl
          placeholder="Line 3"
          value={values.points ? values.points[2] : ""}
          onChange={(e) => {
            handlePointUpdate(e.target.value, 2);
          }}
        />
      </div>
    </div>
  );
  const projectBody = (
    <div className={styles.detail}>
      <div className={styles.row}>
        <InputControl
          label="Project Title"
          placeholder="Enter Project Title"
          value={values.title}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, title: e.target.value }));
          }}
        />
      </div>
      <InputControl
        label="Overview"
        placeholder="Enter Overview"
        value={values.overview}
        onChange={(e) => {
          setValues((prev) => ({ ...prev, overview: e.target.value }));
        }}
      />
      <div className={styles.row}>
        <InputControl
          label="Deployment Link"
          placeholder="Enter Deployment Link"
          value={values.deploymentLink}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, deploymentLink: e.target.value }));
          }}
        />
        <InputControl
          label="Github Repo Link"
          placeholder="Enter Github Repo Link"
          value={values.repoLink}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, repoLink: e.target.value }));
          }}
        />
      </div>
      <div className={styles.column}>
        <label>Enter Project Description</label>
        <InputControl
          placeholder="Line 1"
          value={values.points ? values.points[0] : ""}
          onChange={(e) => {
            handlePointUpdate(e.target.value, 0);
          }}
        />
        <InputControl
          placeholder="Line 2"
          value={values.points ? values.points[1] : ""}
          onChange={(e) => {
            handlePointUpdate(e.target.value, 1);
          }}
        />
        <InputControl
          placeholder="Line 3"
          value={values.points ? values.points[2] : ""}
          onChange={(e) => {
            handlePointUpdate(e.target.value, 2);
          }}
        />
      </div>
    </div>
  );
  const certificatesBody = (
    <div className={styles.detail}>
      <div className={styles.row}>
        <InputControl
          label="Certificate"
          placeholder="Enter Certificate Name"
          value={values.title}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, title: e.target.value }));
          }}
        />
      </div>
      <div className={styles.row}>
        <InputControl
          label="Issued By"
          placeholder="Certificate Issued By"
          value={values.issuedBy}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, issuedBy: e.target.value }));
          }}
        />
        <InputControl
          label="Issued On"
          value={values.issuedOn}
          type="Date"
          onChange={(e) => {
            setValues((prev) => ({ ...prev, issuedOn: e.target.value }));
          }}
        />
      </div>
      <div className={styles.row}>
        <InputControl
          label="Certificate Link"
          placeholder="Enter Certificate Link"
          value={values.certificateLink}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, certificateLink: e.target.value }));
          }}
        />
      </div>
    </div>
  );
  const educationBody = (
    <div className={styles.detail}>
      <div className={styles.row}>
        <InputControl
          label="Degree"
          placeholder="Enter Degree"
          value={values.title}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, title: e.target.value }));
          }}
        />
      </div>
      <InputControl
        label="School/College Name"
        placeholder="Enter School/College Name"
        value={values.college}
        onChange={(e) => {
          setValues((prev) => ({ ...prev, college: e.target.value }));
        }}
      />
      <div className={styles.row}>
        <InputControl
          label="CGPA"
          placeholder="Enter your CGPA"
          value={values.cgpa}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, cgpa: e.target.value }));
          }}
        />
        <InputControl
          label="Start Year"
          type="number"
          placeholder="Enter Start Date"
          value={values.startYear}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, startYear: e.target.value }));
          }}
        />
        <InputControl
          label="End Year"
          type="number"
          placeholder="Enter End Date"
          value={values.endYear}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, endYear: e.target.value }));
          }}
        />
      </div>
    </div>
  );
  const basicInfoBody = (
    <div className={styles.detail}>
      <div className={styles.row}>
        <InputControl
          label="Name"
          placeholder="Enter Name"
          value={values.name}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, name: e.target.value }));
          }}
        />
        <InputControl
          label="Title"
          placeholder="Enter Title"
          value={values.title}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, title: e.target.value }));
          }}
        />
      </div>
      <div className={styles.row}>
        <InputControl
          label="Email"
          placeholder="Enter Email"
          value={values.email}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, email: e.target.value }));
          }}
        />
        <InputControl
          label="Phone"
          placeholder="Enter Phone"
          value={values.phone}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, phone: e.target.value }));
          }}
        />
      </div>
      <div className={styles.row}>
        <InputControl
          label="Linkedin Link"
          placeholder="Enter Linkedin Link"
          value={values.linkedin}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, linkedin: e.target.value }));
          }}
        />
        <InputControl
          label="Github Link"
          placeholder="Enter Github Link"
          value={values.github}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, github: e.target.value }));
          }}
        />
      </div>
    </div>
  );
  const skillsBody = (
    <div className={styles.detail}>
      <div className={styles.row}>
        <InputControl
          label="Skill"
          placeholder="Enter Skill"
          value={values.title}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, title: e.target.value }));
          }}
        />
      </div>
      <div className={styles.row}>
        <InputControl
          label="Level"
          placeholder="Enter Level e.g (Beginner, Intermediate, Advanced)"
          type="text"
          value={values.level}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, level: e.target.value }));
          }}
        />
      </div>
    </div>
  );
  const otherBody = (
    <div className={styles.detail}>
      <div className={styles.row}>
        <InputControl
          label="Other Details"
          placeholder="Enter Other"
          type="text"
          value={values.other}
          onChange={(e) => {
            setValues((prev) => ({ ...prev, other: e.target.value }));
          }}
        />
      </div>
    </div>
  );
  const generateBody = () => {
    switch (sections[activeSectionKey]) {
      case sections.basicInfo:
        return basicInfoBody;
      case sections.workExperience:
        return workExperienceBody;
      case sections.education:
        return educationBody;
      case sections.skills:
        return skillsBody;
      case sections.projects:
        return projectBody;
      case sections.certificates:
        return certificatesBody;
      case sections.other:
        return otherBody;
      default:
        return null;
    }
  };
  useEffect(() => {
    const activeInfo = information[sections[activeSectionKey]];
    setActiveInformation(activeInfo);
    setSectionTitle(sections[activeSectionKey]);
    setActiveDetailIndex(0);
    setValues({
      name: activeInfo?.detail?.name || "",
      overview: activeInfo?.details
        ? activeInfo.details[0]?.overview || ""
        : "",
      deploymentLink: activeInfo?.details
        ? activeInfo.details[0]?.deploymentLink || ""
        : "",
      startDate: activeInfo?.details
        ? activeInfo.details[0]?.startDate || ""
        : "",
      endDate: activeInfo?.details ? activeInfo.details[0]?.endDate || "" : "",
      startYear: activeInfo?.details
        ? activeInfo.details[0]?.startYear || ""
        : "",
      endYear: activeInfo?.details ? activeInfo.details[0]?.endYear || "" : "",
      issuedOn: activeInfo?.details
        ? activeInfo.details[0]?.issuedOn || ""
        : "",
      points: activeInfo?.details
        ? activeInfo.details[0]?.points
          ? [...activeInfo.details[0]?.points]
          : ""
        : activeInfo?.points
        ? [...activeInfo.points]
        : "",
      companyName: activeInfo?.details
        ? activeInfo.details[0]?.companyName || ""
        : "",
      position: activeInfo?.details
        ? activeInfo.details[0]?.position || ""
        : "",
      certificateLink: activeInfo?.details
        ? activeInfo.details[0]?.certificateLink || ""
        : "",
      link: activeInfo?.details ? activeInfo.details[0]?.link || "" : "",
      location: activeInfo?.details
        ? activeInfo.details[0]?.location || ""
        : "",
      level: activeInfo?.details ? activeInfo.details[0]?.level || "" : "",
      college: activeInfo?.details ? activeInfo.details[0]?.college || "" : "",
      cgpa: activeInfo?.details ? activeInfo.details[0]?.cgpa || "" : "",
      issuedBy: activeInfo?.details
        ? activeInfo.details[0]?.issuedBy || ""
        : "",
      title: activeInfo?.details
        ? activeInfo.details[0]?.title || ""
        : activeInfo?.detail?.title || "",
      email: activeInformation?.detail?.email || "",
      phone: activeInfo?.detail?.phone || "",
      linkedin: activeInfo?.detail?.linkedin || "",
      github: activeInfo?.details
        ? activeInfo.details[0]?.github || ""
        : activeInfo?.detail?.github || "",
      repoLink: activeInfo?.details
        ? activeInfo.details[0]?.repoLink || ""
        : activeInfo?.detail?.repoLink || "",
      other: activeInfo?.details ? activeInfo.details[0]?.other || "" : "",
    });
  }, [activeSectionKey]);
  useEffect(() => {
    setActiveInformation(information[sections[activeSectionKey]]);
  }, [information]);
  useEffect(() => {
    const details = activeInformation?.details;
    if (!details) return;
    const activeInfo = information[sections[activeSectionKey]];
    setValues({
      overview: activeInfo.details[activeDetailIndex]?.overview || "",
      deploymentLink:
        activeInfo.details[activeDetailIndex]?.deploymentLink || "",
      startDate: activeInfo.details[activeDetailIndex]?.startDate || "",
      endDate: activeInfo.details[activeDetailIndex]?.endDate || "",
      startYear: activeInfo.details[activeDetailIndex]?.startYear || "",
      endYear: activeInfo.details[activeDetailIndex]?.endYear || "",
      issuedOn: activeInfo.details[activeDetailIndex]?.issuedOn || "",
      points: activeInfo.details[activeDetailIndex]?.points || "",
      companyName: activeInfo.details[activeDetailIndex]?.companyName || "",
      position: activeInfo.details[activeDetailIndex]?.position || "",
      certificateLink:
        activeInfo.details[activeDetailIndex]?.certificateLink || "",
      link: activeInfo.details[activeDetailIndex]?.link || "",
      location: activeInfo.details[activeDetailIndex]?.location || "",
      level: activeInfo.details[activeDetailIndex]?.level || "",
      college: activeInfo.details[activeDetailIndex]?.college || "",
      cgpa: activeInfo.details[activeDetailIndex]?.cgpa || "",
      issuedBy: activeInfo.details[activeDetailIndex]?.issuedBy || "",
      title: activeInfo.details[activeDetailIndex]?.title || "",
      linkedin: activeInfo.details[activeDetailIndex]?.linkedin || "",
      github: activeInfo.details[activeDetailIndex]?.github || "",
      repoLink: activeInfo.details[activeDetailIndex]?.repoLink || "",
      other: activeInfo.details[activeDetailIndex]?.other || "",
    });
  }, [activeDetailIndex]);
  return (
    <div className={styles.container}>
      <div className={styles.header}>
        {Object.keys(sections).map((key) => (
          <div
            className={`${styles.section} ${
              activeSectionKey === key ? styles.active : ""
            }`}
            key={key}
            onClick={() => {
              setActiveSectionKey(key);
              handleSubmission();
            }}
          >
            {sections[key]}
          </div>
        ))}
      </div>
      <div className={styles.body}>
        <InputControl
          label="Title"
          placeholder="Enter Section Title"
          value={sectionTitle}
          onChange={(e) => setSectionTitle(e.target.value)}
        />
        <div className={styles.chips}>
          {activeInformation?.details
            ? activeInformation?.details?.map((item, index) => (
                <div
                  className={`${styles.chip} ${
                    activeDetailIndex === index ? styles.active : ""
                  }`}
                  key={index}
                  onClick={() => setActiveDetailIndex(index)}
                >
                  <p>
                    {sections[activeSectionKey]} {index + 1}
                  </p>
                  <X
                    onClick={(event) => {
                      event.stopPropagation();
                      handleDeleteDetail(index);
                    }}
                  />
                </div>
              ))
            : ""}
          {activeInformation?.details &&
          activeInformation?.details?.length > 0 ? (
            <div className={styles.new} onClick={handleAddNew}>
              +Add
            </div>
          ) : (
            ""
          )}
        </div>
        {generateBody()}
        <button onClick={handleSubmission}>Save</button>
      </div>
    </div>
  );
}

export default Editor;
