import React, { forwardRef, useEffect, useRef, useState } from "react";
import styles from "./Resume.module.css";
import {
  AtSign,
  Calendar,
  ExternalLink,
  GitHub,
  Linkedin,
  MapPin,
  Phone,
} from "react-feather";

const Resume = forwardRef((props, ref) => {
  const information = props.information;
  const sections = props.sections;
  const selectedColor = props.selectedColor;
  const [columns, setColumns] = useState([[], []]);
  const [source, setSource] = useState("");
  const [target, setTarget] = useState("");
  const containerRef = useRef();
  const info = {
    basicInfo: information[sections.basicInfo],
    workExperience: information[sections.workExperience],
    education: information[sections.education],
    skills: information[sections.skills],
    projects: information[sections.projects],
    certificates: information[sections.certificates],
    other: information[sections.other],
  };
  const getFormattedDate = (date) => {
    if (!date) return "";
    const newDate = new Date(date);
    return `${
      newDate.getDate() < 10 ? `0${newDate.getDate()}` : newDate.getDate()
    }/${
      newDate.getMonth() + 1 > 9
        ? newDate.getMonth() + 1
        : `0${newDate.getMonth() + 1}`
    }/${newDate.getFullYear()}`;
  };
  const sectionDiv = {
    [sections.workExperience]: (
      <div
        draggable
        onDragOver={() => setTarget(info.workExperience?.id)}
        onDragEnd={() => setSource(info.workExperience?.id)}
        key={"workExperienceSection"}
        className={`${info.workExperience?.sectionTitle ? "" : styles.hidden} ${
          styles.section
        } `}
      >
        <div className={styles.sectionTitle}>
          {info.workExperience.sectionTitle}
        </div>
        <div className={`${styles.content} ${styles.twoColumn}`}>
          {info.workExperience?.details?.map((item, index) => (
            <div key={index} className={styles.item}>
              <p className={styles.title}>{item.title}</p>
              <p className={styles.subTitle}>{item.companyName}</p>
              {item.location ? (
                <p className={styles.date}>
                  <MapPin />
                  {item.location}
                </p>
              ) : null}
              {item.startDate && item.endDate ? (
                <div className={styles.date}>
                  <Calendar /> {getFormattedDate(item.startDate)} -{" "}
                  {getFormattedDate(item.endDate)}
                </div>
              ) : null}
              {item.points?.length > 0 ? (
                <ul className={styles.points}>
                  {item.points.map((point, index) => (
                    <li key={index} className={styles.point}>
                      {point}
                    </li>
                  ))}
                </ul>
              ) : null}
            </div>
          ))}
        </div>
      </div>
    ),
    [sections.education]: (
      <div
        draggable
        onDragOver={() => setTarget(info.education?.id)}
        onDragEnd={() => setSource(info.education?.id)}
        key={"educationSection"}
        className={`${info.education?.sectionTitle ? "" : styles.hidden} ${
          styles.section
        }`}
      >
        <div className={styles.sectionTitle}>{info.education.sectionTitle}</div>
        <div className={`${styles.content} ${styles.twoColumn}`}>
          {info.education?.details?.map((item, index) => (
            <div key={index} className={styles.item}>
              <p className={styles.title}>{item.title}</p>
              <p className={styles.subTitle}>{item.college}</p>
              {item.cgpa ? (
                <p className={styles.overview}>{item.cgpa}</p>
              ) : null}

              {item.startYear && item.endYear ? (
                <div className={styles.date}>
                  <Calendar />
                  {item.startYear} - {item.endYear}
                </div>
              ) : null}
            </div>
          ))}
        </div>
      </div>
    ),
    [sections.skills]: (
      <div
        draggable
        onDragOver={() => setTarget(info.skills?.id)}
        onDragEnd={() => setSource(info.skills?.id)}
        key={"skillsSection"}
        className={`${info.skills?.sectionTitle ? "" : styles.hidden} ${
          styles.section
        }`}
      >
        <div className={styles.sectionTitle}>{info.skills.sectionTitle}</div>
        <div className={`${styles.content} ${styles.twoColumn}`}>
          {info.skills?.details?.map((item, index) => (
            <div key={index} className={`${styles.item} ${styles.noborder}`}>
              <p className={styles.title}>{item.title}</p>
              <p className={styles.overview}>{item.level}</p>
            </div>
          ))}
        </div>
      </div>
    ),
    [sections.projects]: (
      <div
        draggable
        onDragOver={() => setTarget(info.projects?.id)}
        onDragEnd={() => setSource(info.projects?.id)}
        key={"projectsSection"}
        className={`${info.projects?.sectionTitle ? "" : styles.hidden} ${
          styles.section
        }`}
      >
        <div className={styles.sectionTitle}>{info.projects.sectionTitle}</div>
        <div className={`${styles.content} ${styles.twoColumn}`}>
          {info.projects?.details?.map((item, index) => (
            <div key={index} className={styles.item}>
              <p className={styles.title}>{item.title}</p>
              {item.deploymentLink ? (
                <a
                  className={styles.link}
                  href={item.deploymentLink}
                  target="_blank"
                >
                  <ExternalLink />
                  {item.deploymentLink}
                </a>
              ) : null}
              {item.repoLink ? (
                <a className={styles.link} href={item.repoLink} target="_blank">
                  <GitHub />
                  {item.repoLink}
                </a>
              ) : null}
              {item.overview ? (
                <p className={styles.overview}>{item.overview}</p>
              ) : null}
              {item.points?.length > 0 ? (
                <ul className={styles.points}>
                  {item.points.map((point, index) => (
                    <li key={index} className={styles.point}>
                      {point}
                    </li>
                  ))}
                </ul>
              ) : null}
            </div>
          ))}
        </div>
      </div>
    ),
    [sections.certificates]: (
      <div
        draggable
        onDragOver={() => setTarget(info.certificates?.id)}
        onDragEnd={() => setSource(info.certificates?.id)}
        key={"certificatesSection"}
        className={`${info.certificates?.sectionTitle ? "" : styles.hidden} ${
          styles.section
        }`}
      >
        <div className={styles.sectionTitle}>
          {info.certificates.sectionTitle}
        </div>
        <div className={`${styles.content} ${styles.twoColumn}`}>
          {info.certificates?.details?.map((item, index) => (
            <div key={index} className={styles.item}>
              <a
                className={`${styles.title} ${styles.certificateLink}`}
                href={item.certificateLink}
                target="_blank"
                rel="noreferrer"
              >
                {item.title}
              </a>
              <p className={styles.overview}>{item.issuedBy}</p>
              {item.date ? (
                <div className={styles.date}>
                  <Calendar /> {getFormattedDate(item.issuedOn)}
                </div>
              ) : null}
            </div>
          ))}
        </div>
      </div>
    ),
    [sections.other]: (
      <div
        draggable
        onDragOver={() => setTarget(info.other?.id)}
        onDragEnd={() => setSource(info.other?.id)}
        key={"otherSection"}
        className={`${info.other?.sectionTitle ? "" : styles.hidden} ${
          styles.section
        }`}
      >
        <div className={styles.sectionTitle}>{info.other.sectionTitle}</div>
        <div className={styles.content}>
          {info.other?.details?.map((item, index) => (
            <div key={index} className={styles.item}>
              <p className={styles.title}>{item.other}</p>
            </div>
          ))}
        </div>
      </div>
    ),
  };
  const swapSourceTarget = (source, target) => {
    if (!source || !target) return;
    const tempColumns = [[...columns[0]], [...columns[1]]];
    let sourceRowIndex = tempColumns[0].findIndex((item) => item === source);
    let sourceColumnIndex = 0;
    if (sourceRowIndex < 0) {
      sourceColumnIndex = 1;
      sourceRowIndex = tempColumns[1].findIndex((item) => item === source);
    }
    let targetRowIndex = tempColumns[0].findIndex((item) => item === target);
    let targetColumnIndex = 0;
    if (targetRowIndex < 0) {
      targetColumnIndex = 1;
      targetRowIndex = tempColumns[1].findIndex((item) => item === target);
    }
    const temp = tempColumns[sourceColumnIndex][sourceRowIndex];
    tempColumns[sourceColumnIndex][sourceRowIndex] =
      tempColumns[targetColumnIndex][targetRowIndex];
    tempColumns[targetColumnIndex][targetRowIndex] = temp;
    setColumns(tempColumns);
    setSource("");
    setTarget("");
  };
  useEffect(() => {
    setColumns([
      [sections.education, sections.workExperience, sections.skills],
      [sections.certificates, sections.projects, sections.other],
    ]);
  }, []);
  useEffect(() => {
    swapSourceTarget(source, target);
  }, [source]);
  useEffect(() => {
    const container = containerRef.current;
    if (!selectedColor || !container) return;
    container.style.setProperty("--color", selectedColor);
  }, [selectedColor]);
  return (
    <div ref={ref}>
      <div ref={containerRef} className={styles.container}>
        <div className={styles.header}>
          <p className={styles.heading}>{info.basicInfo?.detail?.name} </p>
          <p className={styles.subheading}>{info.basicInfo?.detail?.title}</p>
          <div className={styles.links}>
            {info.basicInfo?.detail?.email ? (
              <a
                className={styles.link}
                href={`mailto:${info.basicInfo?.detail?.email}`}
                target="_blank"
                rel="noreferrer"
              >
                <AtSign />
                {info.basicInfo?.detail?.email}
              </a>
            ) : null}
            {info.basicInfo?.detail?.phone ? (
              <a
                className={styles.link}
                href={`tel:${info.basicInfo?.detail?.phone}`}
                target="_blank"
                rel="noreferrer"
              >
                <Phone />
                {info.basicInfo?.detail?.phone}
              </a>
            ) : null}

            {info.basicInfo?.detail?.linkedin ? (
              <a
                className={styles.link}
                href={info.basicInfo?.detail?.linkedin}
                target="_blank"
                rel="noreferrer"
              >
                <Linkedin />
                {info.basicInfo?.detail?.linkedin}
              </a>
            ) : null}
            {info.basicInfo?.detail?.github ? (
              <a
                className={styles.link}
                href={info.basicInfo?.detail?.github}
                target="_blank"
                rel="noreferrer"
              >
                <GitHub />
                {info.basicInfo?.detail?.github}
              </a>
            ) : null}
          </div>
        </div>
        <div className={styles.main}>
          <div className={styles.col1}>
            {columns[0].map((item) => sectionDiv[item])}
          </div>
          <div className={styles.col2}>
            {columns[1].map((item) => sectionDiv[item])}
          </div>
        </div>
      </div>
    </div>
  );
});

export default Resume;
