import React, { useEffect, useRef, useState } from "react";
import styles from "./Body.module.css";
import { ChromePicker } from "react-color";
import { BsPaintBucket, BsDownload } from "react-icons/bs";
import Editor from "../Editor/Editor";
import Resume from "../Resume/Resume";
import ReactToPrint from "react-to-print";
function Body() {
  const colors = ["#239ce2", "#f284d3", "#A9A9A9", "#6B8E23", "#9370DB"];
  const [selectedColor, setSelectedColor] = useState("#239ce2");
  const [showColorPalette, setShowColorPalette] = useState(false);
  useEffect(() => {}, [selectedColor]);
  const sections = {
    basicInfo: "Basic  Info",
    workExperience: "Work Experience",
    education: "Education",
    skills: "Skills",
    projects: "Projects",
    certificates: "Certificates",
    other: "Others",
  };
  const resumeRef = useRef();
  const [resumeInformation, setResumeInformation] = useState({
    [sections.basicInfo]: {
      id: sections.basicInfo,
      sectionTitle: sections.basicInfo,
      detail: {},
    },
    [sections.workExperience]: {
      id: sections.workExperience,
      sectionTitle: sections.workExperience,
      details: [],
    },
    [sections.education]: {
      id: sections.education,
      sectionTitle: sections.education,
      details: [],
    },
    [sections.skills]: {
      id: sections.skills,
      sectionTitle: sections.skills,
      details: [],
    },
    [sections.projects]: {
      id: sections.projects,
      sectionTitle: sections.projects,
      details: [],
    },
    [sections.certificates]: {
      id: sections.certificates,
      sectionTitle: sections.certificates,
      details: [],
    },
    [sections.other]: {
      id: sections.other,
      sectionTitle: sections.other,
      details: [],
    },
  });
  const handleColorChange = (color) => {
    setSelectedColor(color.hex);
  };
  const chooseColor = (color) => {
    setSelectedColor(color);
  };
  const toggleColorPalette = () => {
    setShowColorPalette(!showColorPalette);
  };
  useEffect(() => {}, [resumeInformation]);
  return (
    <div className={styles.container}>
      <p className={styles.heading}>Resume Builder</p>
      <div className={styles.toolbar}>
        <div className={styles.colors}>
          {colors.map((color, index) => (
            <div
              key={index}
              style={{ backgroundColor: color }}
              className={`${styles.color} 
              ${selectedColor === color ? styles.active : ""}`}
              onClick={() => chooseColor(color)}
            />
          ))}
          <BsPaintBucket
            alt="Paintbrush"
            className={`${styles.paintbrush} ${
              showColorPalette ? styles.active : ""
            }`}
            onClick={toggleColorPalette}
          />
          {showColorPalette ? (
            <ChromePicker
              color={selectedColor}
              onChange={handleColorChange}
              disableAlpha={true}
            />
          ) : null}
        </div>
        <ReactToPrint
          trigger={() => {
            return (
              <button className={styles.button}>
                Download
                <BsDownload />
              </button>
            );
          }}
          content={() => resumeRef.current}
        />
      </div>
      <div className={styles.main}>
        <Editor
          sections={sections}
          information={resumeInformation}
          setInformation={setResumeInformation}
        />

        <Resume
          ref={resumeRef}
          sections={sections}
          information={resumeInformation}
          selectedColor={selectedColor}
        />
      </div>
    </div>
  );
}

export default Body;
